.photobox{
    display: inline-block;
    width: 100%;
}
.photobox__previewbox{
    position: relative;
    overflow: hidden;
    max-height: 300px;
}

.photobox__preview{
    display: block;
    max-width: 100%;
}

.photobox__previewbox:before{
    content: "";
}

.photobox_type16 .photobox__previewbox:before{
    width: 100%;
    height: 100%;

    background-color: var(--photoboxOverlay, rgba(0, 0, 0, .8));
    opacity: 0;
    transition: opacity var(--photoboxAnimationDuration, .4s) cubic-bezier(0.71, 0.05, 0.29, 0.9);
    will-change: opacity;

    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
}

.photobox_type16:hover .photobox__previewbox:before{
    opacity: 1;
}

.photobox_type16 .photobox__label{
    width: 98%;
    text-align: center;

    opacity: 0;
    transition: opacity calc(var(--photoboxAnimationDuration, .4s) / 4) cubic-bezier(0.71, 0.05, 0.29, 0.9);
    will-change: opacity;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
}

.photobox_type16:hover .photobox__label{
    opacity: 1;
    transition-duration: var(--photoboxAnimationDuration, .4s);
    transition-delay: calc(var(--photoboxAnimationDuration, .4s) / 2);
}

.photobox_type16 .photobox__preview{
    transition: transform var(--photoboxAnimationDuration, .4s) cubic-bezier(0.71, 0.05, 0.29, 0.9);
    will-change: transform;
    transform: scale(1) translate(0, 0);
    height: 100%;
    min-height: 300px;
}
.photobox{
    color: #fff;
    font-size: 2.5rem;
    font-weight: 700;
    width: 100%;
    height: 100%;
    --photoboxOverlay: rgba(0, 5, 15, 0.82);
    /*--photoboxAnimationDuration: .5s;*/
}

@media screen and (max-width: 480px){
    .photobox{
        width: 100%;
    }
}

