@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Nunito+Sans&display=swap);
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.photobox{
    display: inline-block;
    width: 100%;
}
.photobox__previewbox{
    position: relative;
    overflow: hidden;
    max-height: 300px;
}

.photobox__preview{
    display: block;
    max-width: 100%;
}

.photobox__previewbox:before{
    content: "";
}

.photobox_type16 .photobox__previewbox:before{
    width: 100%;
    height: 100%;

    background-color: rgba(0, 0, 0, .8);

    background-color: var(--photoboxOverlay, rgba(0, 0, 0, .8));
    opacity: 0;
    -webkit-transition: opacity .4s cubic-bezier(0.71, 0.05, 0.29, 0.9);
    transition: opacity .4s cubic-bezier(0.71, 0.05, 0.29, 0.9);
    -webkit-transition: opacity var(--photoboxAnimationDuration, .4s) cubic-bezier(0.71, 0.05, 0.29, 0.9);
    transition: opacity var(--photoboxAnimationDuration, .4s) cubic-bezier(0.71, 0.05, 0.29, 0.9);
    will-change: opacity;

    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
}

.photobox_type16:hover .photobox__previewbox:before{
    opacity: 1;
}

.photobox_type16 .photobox__label{
    width: 98%;
    text-align: center;

    opacity: 0;
    -webkit-transition: opacity calc(.4s / 4) cubic-bezier(0.71, 0.05, 0.29, 0.9);
    transition: opacity calc(.4s / 4) cubic-bezier(0.71, 0.05, 0.29, 0.9);
    -webkit-transition: opacity calc(var(--photoboxAnimationDuration, .4s) / 4) cubic-bezier(0.71, 0.05, 0.29, 0.9);
    transition: opacity calc(var(--photoboxAnimationDuration, .4s) / 4) cubic-bezier(0.71, 0.05, 0.29, 0.9);
    will-change: opacity;

    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 2;
}

.photobox_type16:hover .photobox__label{
    opacity: 1;
    -webkit-transition-duration: .4s;
            transition-duration: .4s;
    -webkit-transition-duration: var(--photoboxAnimationDuration, .4s);
            transition-duration: var(--photoboxAnimationDuration, .4s);
    -webkit-transition-delay: calc(.4s / 2);
            transition-delay: calc(.4s / 2);
    -webkit-transition-delay: calc(var(--photoboxAnimationDuration, .4s) / 2);
            transition-delay: calc(var(--photoboxAnimationDuration, .4s) / 2);
}

.photobox_type16 .photobox__preview{
    -webkit-transition: -webkit-transform .4s cubic-bezier(0.71, 0.05, 0.29, 0.9);
    transition: -webkit-transform .4s cubic-bezier(0.71, 0.05, 0.29, 0.9);
    transition: transform .4s cubic-bezier(0.71, 0.05, 0.29, 0.9);
    transition: transform .4s cubic-bezier(0.71, 0.05, 0.29, 0.9), -webkit-transform .4s cubic-bezier(0.71, 0.05, 0.29, 0.9);
    -webkit-transition: -webkit-transform var(--photoboxAnimationDuration, .4s) cubic-bezier(0.71, 0.05, 0.29, 0.9);
    transition: -webkit-transform var(--photoboxAnimationDuration, .4s) cubic-bezier(0.71, 0.05, 0.29, 0.9);
    transition: transform var(--photoboxAnimationDuration, .4s) cubic-bezier(0.71, 0.05, 0.29, 0.9);
    transition: transform var(--photoboxAnimationDuration, .4s) cubic-bezier(0.71, 0.05, 0.29, 0.9), -webkit-transform var(--photoboxAnimationDuration, .4s) cubic-bezier(0.71, 0.05, 0.29, 0.9);
    will-change: transform;
    -webkit-transform: scale(1) translate(0, 0);
            transform: scale(1) translate(0, 0);
    height: 100%;
    min-height: 300px;
}
.photobox{
    color: #fff;
    font-size: 2.5rem;
    font-weight: 700;
    width: 100%;
    height: 100%;
    --photoboxOverlay: rgba(0, 5, 15, 0.82);
    /*--photoboxAnimationDuration: .5s;*/
}

@media screen and (max-width: 480px){
    .photobox{
        width: 100%;
    }
}

